section.reservation{
  display: block;
  justify-content: center;

  .section-header{
    padding-left:0;
    padding-right:0;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  form {
    fieldset{
      border-radius: 5px;
    }

    section{
      display: flex;
      padding: 20px;
      justify-content: space-between;
      align-items: center;
    }

    .invalid{
      border-color: red;
      border-style:inset;
    }

    input{
      border-style: none;
    }

    button{
      margin: 30px 20%;
      width: 60%;
    }

    label{
      min-width: 260px;
    }

    section.field-error{
      justify-content: center;
      label.error{
        text-align: center;
        font-size: 20px;
        font-family: 'Karla';
        color: red;
      }
    }

    input.h3-like, select.h3-like{
      color: var(--secondary-colorBlack);
      width: 300px;
      border-radius: 4px;
      box-sizing: border-box;
      padding: 8px;
    }
  }

}